<template>
<div class='wrapper'>
 <v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
 
 <div class="borderform">
 <span class="title">课程信息</span>
     <div id="infoform">
<el-form id="form1" :inline="true" :rules="rules" :model="form1" ref="form1" label-width="100px" >
     <el-form-item label="ID：" prop="cid" >
        <el-input v-model="form1.cid" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="名字：" prop="cname">
        <el-input v-model="form1.cname" type="text" style="width:220px"></el-input>
     </el-form-item>
    </el-form-item>
	<el-form-item label="教师名字：" prop="tname">
        <el-input v-model="form1.tname" style="width:220px"  type="text"></el-input>
     </el-form-item>
    <el-form-item label="类型：" prop="ctype">
        <el-input v-model="form1.ctype" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="学分：" prop="credict">
        <el-input v-model="form1.credict" style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="总学时：" prop="totalTime">
        <el-input v-model="form1.totalTime" style="width:220px"></el-input>
     </el-form-item>
         <el-form-item label="学期：" prop="learnTerm">
        <el-date-picker v-model="form1.learnTerm" type="text" style="width:220px"></el-date-picker>
     </el-form-item>
    </el-form-item>
         <el-form-item label="学年：" prop="learnYear">
        <el-date-picker v-model="form1.learnYear" type="text" style="width:220px"></el-date-picker>
     </el-form-item>
    </el-form-item>
    <el-form-item label="课室：" prop="classroom">
        <el-input v-model="form1.classroom" type="text" style="width:220px"></el-input>
    </el-form-item>
    <el-form-item label="depID：" prop="depID">
        <el-input v-model="form1.depID" type="text" style="width:220px"></el-input>
    </el-form-item>
         <el-form-item label="时间ID：" prop="timeSlotID">
        <el-date-picker v-model="form1.timeSlotID" type="text" style="width:220px"></el-date-picker>
     </el-form-item>
        <el-form-item label="教师ID：" prop="proID">
        <el-input v-model="form1.proID" style="width:220px"  type="text"></el-input>
     </el-form-item>
        <el-form-item label="管理员ID：" prop="admID">
        <el-input v-model="form1.admID" style="width:220px"  type="text"></el-input>
     </el-form-item>
        <el-form-item label="学生人数：" prop="stuNumber">
        <el-input v-model="form1.stuNumber" style="width:220px"  type="text"></el-input>
     </el-form-item>    
         <el-form-item label="容量：" prop="quantity">
        <el-input v-model="form1.quantity"  type="text" style="width:220px"></el-input>
     </el-form-item>
<el-col></el-col>
</el-form>
</div>

<el-form id="form2" :inline="true" label-width="150px" >
<el-form-item>
        <el-button type="primary" @click="modifyForm('form1')">修改</el-button>
                
                 <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
    
     </el-form>
        </div>
      </div>
    </div>
   </div>
</div>
</template>
    
<script>
 import vHead from '../commons/AdminstratorNav.vue';
export default {
  components:{
      vHead,
  },
  data(){
  
      return{
          hostUrl:'/',
          check1: false,
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
          form1:{
              cid: '',
              cname: '',
			  tname:'',
              ctype:'',
              credict:'',
              totalTime:'',
              learnTerm:'',
              learnYear:'',
              classroom:'',
              depID:'',
              timeSlotID:'',
              proID:'',
              admID:'',
              stuNumber:'',
              quantity:'',
              sarea_of_interest:''
          },
		  rules: {
          cid: [
            { required: true, message: '请输入课程ID', trigger: 'blur' },
            { min: 11, max: 11, message: '长度为11个字符', trigger: 'blur' }
          ],
          cname: [
            { required: true, message: '输入课程名字', trigger: 'blur' },
          ],
          tname: [
            { required: true, message: '输入教师名字', trigger: 'blur' },
          ]

        }
      };
  },
  methods:{
        modifyForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
     
      exit(formName) {
        this.$router.push({path: '/adminstrator/biCourseManagement/'})
      }
    }
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
	
}
#infoform{
    padding-top: 30px;
	
}
#form1{
    width:80%;
    margin:0px auto;
	
}
.borderform{
height:330px;
border:1px solid #909399;
}

#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>



